import { writable } from 'svelte/store';
export const markers = writable([]);
export const addGameMarker = (x, y, name, content = null) => {
    markers.update(markers => {
        markers.push({
            id: markers.length,
            type: 'game',
            x: x,
            y: y,
            label: name,
            content,
        });
        return markers;
    });
};
export const addMarker = (lat, lon, name, content = null) => {
    markers.update(markers => {
        markers.push({
            id: markers.length,
            type: 'normal',
            x: lon,
            y: lat,
            label: name,
            content,
        });
        return markers;
    });
};
export const removeMarker = (id) => {
    markers.update(markers => {
        const index = markers.findIndex(m => m.id === id);
        if (index > -1) {
            markers.splice(index, 1);
        }
        return markers;
    });
};
// debug marker
export const debugMarker = writable([0, 0]);
export const setDebugMarker = (x, y) => debugMarker.update(v => [x, y]);
